.dashboard{
    display: flex;
    padding: 28px 28px 28px 28px;
    flex-direction: column;
    align-items: center;
    gap: 28px;
}

.content {
    width: 100%;
    height: 648px;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 16px;
    }

.main {
    width: 100%;
    height: 618px;
    position: relative;
    backdrop-filter: blur(12px);
    border-radius: 8px;
    background-color: rgba(245, 245, 245, 0.75);
    border: 2px solid #fbfbfb;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 24px;
    gap: 24px;
    text-align: left;
    font-size: 16px;
    color: #163235;
    font-family: Poppins;
    }

.dashboard-head {
    display: flex;
    padding: 12px;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    align-self: stretch;
    border-radius: 8px;
    background: rgba(245, 245, 245, 0.75);
    backdrop-filter: blur(6px);
}
.HeadGrp {
    display: flex;
    width: 100%;
    gap: 16px;
    justify-content: space-between;
}
.main-head {
    width: 100%;
    position: relative;
    box-shadow: 4px 4px 32px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    background: linear-gradient(-17.07deg, #e6e6e6, #f3f3f3 51.26%, #eaeaea);
    border: 3px solid #fff;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    text-align: left;
    font-size: 16px;
    color: #163235;
    font-family: Poppins;
    }

.title {
    flex: 1;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 8px;
    text-align: left;
    font-size: 16px;
    font-weight: 600;
    color: #163235;
    font-family: Poppins;
    }
    
.cases {
    position: relative;
    filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.18));
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 24px;
    text-align: center;
    font-size: 16px;
    color: #007c89;
    font-family: Poppins;
    overflow-y: scroll;
}

.EmptyCard {
    display: flex;
    height: 100%;
    margin: 12px 0px;
    padding: 12px 12px 100px 12px;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    align-self: stretch;
    border-radius: 8px;
    background: linear-gradient(94deg, #FFF -8.92%, #F8F8F8 115.1%);
    box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.06), -2px -2px 9px 0px rgba(0, 0, 0, 0.06);    
}

.Heading2 {
    color: var(--Color-2, #167D7F);
    width: 636px;
    text-align: center;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-wrap: pretty;
}

.Helpbar {
    display: flex;
    padding: 12px;
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
    align-self: stretch;
}

.helper {
    width: 380px;
    color: var(--Color-9, #333);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.iconSizeInContent {
    width: 32px;
    height: 32px;
    scale: 1.2;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #8E8E8E;
}

.PersonNote {
    display: flex;
    align-items: center;
}

::-webkit-scrollbar {
    width: .5rem;
    height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px white;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 10px;
}