.DropDrownMenu.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition: var(--speed) ease;
    transition: 0.3s;
}

.DropDrownMenu.inactive {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20);
    transition: var(--speed) ease;
    transition: 0.5s;
}

.header {
    height: 64px;
    display: flex;
    align-items: center;
    background-color: #FBFBFB;
    position: sticky;
    z-index: 100;
    padding: 16px 36px;
    justify-content: space-between;
    box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.1);
}

.milalogo {
    width: 232px;
    object-fit: contain;
}

.headerNav {
    display: flex;
    flex: 1;    
    justify-content: flex-end;
    align-items: center;
    gap: 32px;
    padding: 0px 0px 0px 30px;
}

.iconSize {
    width: 32px;
    height: 32px;
    scale: 1.5;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #4FBC9C;
}

.SearchSize {
    color: #B3B3B3;
}

.searchBar {
    display: flex;
    width: 30%;
    padding: 4px 12px;
    height: 32px;
    justify-content: flex-start;
    align-items: center;
    border-radius: 8px;
    border: 2px solid var(--Color-5, #e1e1e1);
    background: var(--Color-1, #FBFBFB);
}

.Search {
    display: flex;
}
form {
    display: flex;
    flex: 1;
}

form > input {
    display: flex;
    flex: 1;
    background-color: transparent;
    border: none;
    color: var(--Color-6, #303030);
    text-align: left;
    font-size: 18px;
    font-style: normal;
    font-family: Poppins;
    font-weight: 400;
    line-height: normal;
    padding: 0px 12px;
}

form > button {
    display: none;
}

form > input:focus {
    outline: none;
}

.ProfilePicIcon {
    width: 20px;
    border: 1px solid var(--White, #FFF);
    border-radius: 24px;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.105);
    display: flex;
    align-items: center;
    justify-content: center;
}

.ProfilePicIcon:hover {
    cursor: pointer;
    border: 1px solid var(--White, #106967);
    transition: 0.3s;
}

.ProfilePicIcon:active {
    cursor: pointer;
    border: 1px solid var(--White, #073535);
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.753);
    transition: 0.3s;
}

.ProfileIcon {
    scale: 2;
    color: #106967;
    border: 1px solid var(--White, #FFF);
    border-radius: 24px;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    align-items: center;
    justify-content: center;
}

.ProfileIcon:hover {
    cursor: pointer;
}

.iconSize:hover {
    cursor: pointer;
}

.help:hover {
    cursor: pointer;
}