.Content_Header {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.CaseName {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 12px;
    text-align: center;
    font-size: 38px;
    color: #333;
    font-family: Poppins;
}

h2 {
    color: var(--Color-9, #333);
    text-align: center;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
}

.actions {
    display: flex;
    flex-direction: row;
    gap: 12px;
    align-items: center;
}

.Status {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 8px;
}

.Date {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 8px;
}
