.toggleGRP {
    display: flex;
    padding: 8px 16px;
    justify-content: flex-end;
    align-items: center;
    gap: 32px;
    align-self: stretch;    
    border-radius: 8px;
    border-left: 4px solid var(--Accent, #4FBC9C);
    background: var(--White, #FFF);
    box-shadow: 2px 2px 12px 0px rgba(0, 0, 0, 0.08);    
}

.toggle-btn {
    background-color: #b7b9ba;
    border: 1px solid #aaa;
    border-radius: 99px;
    width: 50px;
    height: 28px;
    transition: background-color 0.1s ease, border-color 0.2s ease;
    cursor: pointer;
    position: relative;
}

.toggle-btn .thumb {
    height: 20px;
    width: 20px;
    background-color: #fff;
    border-radius: 99px;
    transform: translateX(0);
    transition: left 0.15s ease;
    position: absolute;
    left: 3px;
    top: 50%;
    transform: translateY(-50%);
}

.toggle-btn.toggled {
background-color: #15b58e;
}

.toggle-btn:hover {
    border-color: #6f6f6f;
}

.toggle-btn.toggled .thumb {
    left: calc(50px - 25px);
}